import { message } from '@ali/we-design';
import { environment } from '@ali/4ever-cangjie';
import { ClipboardPluginLocale } from '../../utils/types';

const MOD = environment.IS_MAC ? 'âŒ˜' : 'Ctrl';

export default function onCangjiePasteFail(locale?: ClipboardPluginLocale) {
  if (locale?.notSupport && locale.shortcutKey) {
    const pasteKey = ` ${MOD}+V `;
    message.info(locale.notSupport + pasteKey + locale.shortcutKey);
  }
}
